*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}
body{
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background:url(imgs/8716d1a0243fff0827bd0d66a0a8c066.jpeg) no-repeat ;
  background-size: cover;
  background-position: center;


}
.wrapper{
  width: 400px;
  background: transparent;
  color: white;
  padding: 40px 30px;
  border: 2px solid rgba(255, 255, 255, 0.3) ;
  backdrop-filter: blur(20px);
  border-radius: 20px;
}
.wrapper h1{
  font-size: 36px;
 text-align: center; 
}
.wrapper .input-box{
  position: relative;
  width: 100%;
  height: 50px;
  margin: 30px 0;
}
.input-box input{
  width: 100%;
  height: 100%;
  background-color: transparent;
  border: none;
  outline: none;
  border: 2px solid rgba( 255,255 ,255 ,0.2 ) ;
  border-radius: 40px;
  font-size: 16px;
  padding: 20px ;
  color: white;
}
 .input-box input::placeholder{
  color: white;
 }
 .input-box i{
  position: absolute;
  right: 20px;
  top: 50%;
  transform: translateY(-50%);
  font-size: 20px;
 }
 .wrapper .forget{
  display: flex;
 justify-content: space-between;
 font-size: 16px;
 margin: -15px 0 15px;
}
.forget label input{
  margin-right: 4px;
}
.forget a{
  text-decoration: none;
  color: white;
}
.forget a:hover{
  text-decoration: underline;

}
.login-btn{
  width: 100%;
  height: 45px;
  border: none;
  outline: none;
  border-radius: 40px;
  font-size: 20px;
  font-weight: 600;
  color: #333;
  cursor: pointer;

}
.login-btn:hover{
  
  background-color: rgb(207, 102, 75) ;
  transition: 0.3s;
}
.wrapper .register p{
  text-align: center;
  margin-top: 20px;
  font-weight: 500;
  
}
.wrapper .register p a {
 text-decoration: none;
 color: white;
 font-weight: 600;
}
.wrapper .register p a:hover{
  text-decoration: underline;

}
















